import css from "./styles.module.scss"
import cx from "classnames"

const ArcadeButton = ({ isPushed, glow, className, ...props }) => (
  <button {...props} className={cx(className, css.pushFlat, { [css.isPushed]: isPushed, [css.glow]: glow }, css[props.variant])}></button>
)

export default ArcadeButton
